* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  width: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: system-ui, sans-serif;
  background-color: var(--bg);
  color: var(--text);
  transition: background-color 0.3s ease, color 0.3s ease;
}

:root {
  --primary: #3a86ff;
  --bg: #f0f4f8;
  --text: #222;
  --label: #555;
  --card-bg: #fff;
  --card-gradient: linear-gradient(135deg, #e0eafc 0%, #cfdef3 100%);
  --card-radius: 16px;
  --card-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  --transition: 0.3s ease;
}

body.dark {
  --bg: #1e1e1e;
  --text: #f5f5f5;
  --label: #ccc;
  --card-bg: #2a2a2a;
  --card-gradient: linear-gradient(135deg, #2e2e2e 0%, #3b3b3b 100%);
  --card-shadow: 0 8px 24px rgba(0, 0, 0, 0.6);
}

header, footer {
  background-color: #333;
  color: #fff;
  padding: 1rem;
  text-align: center;
  flex-shrink: 0;
}

main {
  flex: 1;
  padding: 2rem 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#app {
  width: 100%;
}

.weather-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.5rem;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 1rem 0;
}


.weather-card {
  background: var(--card-gradient);
  border-radius: var(--card-radius);
  box-shadow: var(--card-shadow);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: transform var(--transition), box-shadow var(--transition);
}

.weather-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.18);
}

.weather-card.today {
  border: 2px solid var(--primary);
}


.card-header {
  background: rgba(255, 255, 255, 0.15);
  padding: 1rem;
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.card-header h3 {
  margin-bottom: 0.25rem;
  color: var(--primary);
  font-size: 1.4rem;
}

.card-header time {
  display: block;
  font-size: 0.9rem;
  color: var(--text);
  opacity: 0.8;
}


.card-body {
  flex: 1;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.description {
  font-style: italic;
  margin-bottom: 1rem;
  color: var(--text);
}


.stats-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.stats-list li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.stats-list strong {
  color: var(--label);
}

#weatherForm {
  display: flex;
  max-width: 500px;
  margin: 2rem auto;
  background: var(--card-bg);
  border-radius: var(--card-radius);
  box-shadow: var(--card-shadow);
  overflow: hidden;
  transition: background var(--transition), box-shadow var(--transition);
}

/* Input */
#weatherForm input {
  flex: 1;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  outline: none;
  background: transparent;
  color: var(--text);
}

/* BotÃ³n */
#weatherForm button {
  padding: 0 1.5rem;
  font-size: 1rem;
  border: none;
  background: var(--primary);
  color: #fff;
  cursor: pointer;
  transition: background var(--transition);
}

#weatherForm button:hover {
  background: darken(var(--primary), 10%);
}


@media (max-width: 768px) {
  main {
    padding: 1rem;
  }
  .weather-grid {
    grid-template-columns: 1fr;
  }
  #weatherForm {
    flex-direction: column;
  }

  #weatherForm input,
  #weatherForm button {
    width: 100%;
    padding: 1rem;
  }

  #weatherForm button {
    border-top: 1px solid rgba(0, 0, 0, 0.05);
  }
}
